body {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0;
  font-size: 15px;
  font-family:'Open Sans', "Arial", sans-serif;
  color: #484848;
  font-style: normal;
  background-color: #ffffff;
}
.visually-hidden:not(:activ):not(:focus){
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
.page-header{
  background-color: #131314;
  background-image: url('../img/Img Bike.jpg');
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position:center 0;
  margin: 0 auto;
  width: 1400px;
  height: 825px;
}

.wrapper{
  width: 874px;
  min-height: 65px;
  background-color: #131314;
  border-bottom:2px solid #d12554 ;
 }
.page__header{
 margin: 0 auto;
}
@media (max-width: 1023px) {
  .wrapper{
    min-height: 50px;
    width: 100%;
  }
}
