body {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0;
  font-size: 15px;
  font-family: 'Open Sans', "Arial", sans-serif;
  color: #484848;
  font-style: normal;
  background-color: #ffffff; }

.visually-hidden:not(:activ):not(:focus) {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.page-header {
  background-color: #131314;
  background-image: url("../img/Img Bike.jpg");
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: center 0;
  margin: 0 auto;
  width: 1400px;
  height: 825px; }

.wrapper {
  width: 874px;
  min-height: 65px;
  background-color: #131314;
  border-bottom: 2px solid #d12554; }

.page__header {
  margin: 0 auto; }

@media (max-width: 1023px) {
  .wrapper {
    min-height: 50px;
    width: 100%; } }

.wrapper {
  width: 100%;
  min-height: 806px; }

.sait-nav__button {
  background-color: #3498DB;
  color: white;
  padding: 16px;
  font-size: 16px;
  border: none;
  cursor: pointer; }

/* Кнопка выпадающего меню при наведении и фокусировке */
.sait-nav__button:hover, .sait-nav__button:focus {
  background-color: #2980B9; }

/* Контейнер <div> - необходим для размещения выпадающего содержимого */
.sait-nav {
  position: relative;
  display: inline-block; }

/* Выпадающее содержимое (скрыто по умолчанию) */
.sait-nav ul {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1; }

/* Ссылки внутри выпадающего списка */
.sait-nav ul a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block; }

/* Изменение цвета выпадающих ссылок при наведении курсора */
.sait-nav ul a:hover {
  background-color: #ddd; }

/* Показать выпадающее меню (используйте JS, чтобы добавить этот класс .dropdown-content содержимого, когда пользователь нажимает на кнопку выпадающего списка) */
.show {
  display: block; }

@media (max-width: 1023px) {
  .myDropdown.opened {
    background-color: #00ff00; } }

.closed {
  background-color: coral; }
  .closed .sait-nav {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .closed .sait-nav__button {
    background-color: azure; }
  .closed .sait-nav__open {
    display: block;
    background-color: #0ad9c6;
    cursor: pointer;
    border: 0;
    height: 50px;
    width: 50px;
    margin-left: auto; }
    .closed .sait-nav__open:hover, .closed .sait-nav__open:active {
      background-color: #c71e1e; }
  .closed .sait-nav--closed .sait-nav__open::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 17px;
    width: 36px;
    height: 2px;
    background-color: #ffffff;
    box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff; }

.sait-nav--open .sait-nav__open {
  top: 0;
  right: auto;
  left: 0; }

.sait-nav--open .sait-nav__open::before,
.sait-nav--open .sait-nav__open::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 18px;
  width: 36px;
  height: 2px;
  background-color: #ffffff; }

.sait-nav--open .sait-nav__open::before {
  transform: rotate(45deg);
  box-shadow: none; }

.sait-nav--open .sait-nav__open::after {
  transform: rotate(-45deg); }

.sait-nav--open .sait-nav__open:active::before,
.sait-nav--open .sait-nav__open:active::after {
  background-color: rgba(255, 255, 255, 0.3); }

.sait-nav ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; }

.sait-nav li {
  padding: 0;
  margin: 0;
  list-style: 0; }

@media (max-width: 1023px) {
  .sait-nav ul {
    width: auto;
    flex-direction: column; } }
